    $color1: #303036;
    $color2: #fc5130;
    $h: 70vh;
    ::-webkit-scrollbar-thumb {
    background: green;
    }
    .Course {
    width: 100vw;
    // height: $h;
    margin-top: 30px;
    // background-color: red;
    overflow: hidden;
    
    .container {
        display: flex;
        justify-content: space-around;
        flex-wrap: nowrap;
        padding: 10px;
        box-sizing: border-box;

        left: 0px;
        .video {
        width: 70vw;
        // height: $h;
        // background-image: url("../picture/video.png");
        border-radius: 20px;
        overflow: hidden;
        video {
            width: 70vw;
            height: $h;
            source{
                content: url(https://www.w3schools.com/tags/movie.mp4);
            }
        }
        }
        hr {
        width: 1px;
        height: $h;
        background-color: $color2;
        border: none;
        }
        .playList {
        width: 20vw;
        height: $h;
        //   background-color: blue;-
        overflow-y: scroll;
        overflow-x: hidden;
        margin: 0 auto;
        

        .item {
            width: 19.5vw;
            max-width: 300px;
            height: 117px;
            background-color: white;
            margin: 0 auto;
            border-radius: 20px;
            display: flex;
            align-items: center;
            padding: 5px;
            box-sizing: border-box;
            margin: 10px auto;
            cursor: pointer;
            img {
            content: url("../picture/JavaScriptLogo.png");
            width: 72px;
            height: 72px;
            border-radius: 50%;
            }
            .text {
            // background-color: aqua;
            margin-left: 7px;
            .title {
                text-decoration: underline;
            }
            .discription {
                // border: 2pt solid gray;
                
            }
            }
            .item:nth-child(1) {
            // margin-top: 0px;
            }
        }
        }
    }
    }
    @media screen and (max-width: 1170px) {
    .Course {
        margin-top: 0px;
        .container {
        // display: block;
        flex-wrap: wrap;
        
        .video {
            width: 99vw;
            max-width: 850px;
            height: 40vh;
            margin: 0 auto;
            box-sizing: border-box;
            // background-color: blue;
            
            video {
            width: 99vw;
            height: 40vh;
            max-width: 850px;
            
            }
        }
        hr {
            visibility: hidden;
            width: 0px;
            height: 0px;
        }
        .playList {
            width: 99vw;
            height: 45vh;
            margin: 0px auto;

            float: left;
            // box-sizing: border-box;
            .item {
            width: 95vw;
            max-width: 95vw;
            height: 117px;
            
            }
        }
        }
    }
    }
