$color1: #303036;
$color2: #fc5130;

.Home {
  // background-color: red;
  // max-height: 150vh;
  width: 75vw;
  margin: 45px auto;
  box-sizing: border-box;
  .container {
    // background-color: red;
    margin: 0 auto;

    width: 75vw;
    margin: 0 auto;

    .part1 {
      display: grid;
      grid-template-columns: auto auto auto;
      grid-template-rows: auto auto;
      // flex-wrap: wrap;
      // align-items: center;

      color: white;
      max-width: 950px;

      margin: 0 auto;

      img:nth-child(1) {
        width: 362px;
        height: 362px;
        content: url("../picture/profile.png");
        // grid-column-start: 1;
        
      }
      hr {
        width: 1px;
        height: 430px;
        background-color: $color2;
        border: none;
        grid-column-start: 2;
        grid-row: 1/3;
        
      }
      span {
        width: 538px;
        height: 337px;
        display: flex;
        justify-content: center;
        align-items: center;
        line-height: 45px;
        text-align: center;
      }

      .socialMedia {
        width: 362px;
        height: 68px;
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        padding: 0px 50px 0px 50px;
        box-sizing: border-box;
        grid-row-start: 2;

        img {
          width: 30px;
          height: 30px;
        }
        .youtube {
          content: url(../picture/YouTube.png);
        }
        .instagram {
          content: url(../picture/Instagram.png);
        }
        .linkdin {
          content: url(../picture/LinkedIn.png);
        }
        .telegram {
          content: url(../picture/TelegramApp.png);
        }
      }
    }
  }
}

@media screen and (max-width: 1225px) {
  .Home {
    margin-top: 80px;

    .container {
      max-width: 800px;
      margin-top: 150px;
      .part1 {
        img:nth-child(1) {
          width: 250px;
          height: 250px;
        }
        hr {
          width: 1px;
          height: 250px;
        }
        span {
          width: 400px;
          height: 250px;
        }

        .socialMedia {
          width: 250px;
          height: 68px;

          img {
            width: 25px;
            height: 25px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .Home {
    margin-top: 120px;

    .container {
      .part1 {
        img:nth-child(1) {
          width: 220px;
          height: 220px;
        }
        hr {
          width: 1px;
          height: 220px;
        }
        span {
          width: 320px;
          height: 220px;
          line-height: 30px;
        }

        .socialMedia {
          width: 220px;
          height: 68px;

          img {
            width: 22px;
            height: 25px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 750px) {
  .Home {
    // background-color: red;
    margin-top: 45px;
    .container {
      // background-color: red;

      width: 75vw;
      margin: 0 auto;

      .part1 {
        display: grid;
        // flex-wrap: wrap;
        // align-items: center;
        // justify-content: space-around;
        grid-template-columns: auto ;
        color: white;
        padding: 0px;
        margin-right: 0px;
        .profile {
          width: 100%;
          text-align: center;
          img:nth-child(1) {
            order: 1;
            width: 260px;
            height: 260px;
            content: url("../picture/profile.png");
          }
        }
        hr {
          order: 6;
          width: 0px;
          height: 0px;
          background-color: $color2;
          border: none;
          visibility: hidden;
        }
        span {
          order: 5;
          width: 74vw;
          height: fit-content;
          display: flex;
          justify-content: center;
          align-items: center;
          line-height: 30px;
          text-align: center;
          margin-top: 7px;
        }

        .socialMedia {
          order: 4;
          width: 250px;
          height: fit-content;
          display: flex;
          justify-content: space-between;
          margin: 7px auto;

          img {
            width: 30px;
            height: 30px;
          }
          .youtube {
            content: url(../picture/YouTube.png);
          }
          .instagram {
            content: url(../picture/Instagram.png);
          }
          .linkdin {
            content: url(../picture/LinkedIn.png);
          }
          .telegram {
            content: url(../picture/TelegramApp.png);
          }
        }
      }
    }
  }
}
