$color1: #303036;
$color2: #fc5130;
.App {
  background-color: #303036;
  width: 100vw;
  height: 100vh;
  display: block;
  justify-content: space-between;
  header {
    width: 100vw;

    display: flex;
    justify-content: space-between;
    .brand {
      padding: 9px;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    div {
      display: flex;
      // padding: 10px;
      $w: 29px;
      $h: 29px;
      padding: 9px;
      // padding-right: 0px;
      img:nth-child(1) {
        content: url(./component/picture/paintPalette.png);
        width: $w;
        height: $h;
        cursor: pointer;
      }
      div:nth-child(2) {
        margin-right: 0px;
      }
      div,
      img {
        margin-right: 17px;
      }
      .lang {
        padding: 0px;
        position: relative;
        width: 26px;
        height: 26px;
        background-color: white;
        top: 50%;
        transform: translate(0%, -50%);
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color1;
        border-radius: 10px;
        cursor: pointer;
      }
    }
  }
  footer {
    position: absolute;
    width: 100vw;
    bottom: 1px;
    // background-color: red;
    .menue {
      width: 75vw;
      height: 61px;

      margin: 0 auto;
      display: flex;
      justify-content: space-around;
      align-items: center;
      color: white;
      span{
        cursor: pointer;
        a{
          text-decoration: none;
          color: white;
        }
      }
      span a:hover{
        color: $color2;
      }
    }
  }
}

@media screen and (max-width: 750px) {
  .App {
    background-color: #303036;
    width: 100vw;
    height: 100vh;
    display: block;
    justify-content: space-between;
    header {
      width: 100vw;

      display: flex;
      justify-content: space-between;
      .brand {
        padding: 9px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      div {
        display: flex;
        // padding: 10px;
        $w: 29px;
        $h: 29px;
        padding: 9px;
        // padding-right: 0px;
        img:nth-child(1) {
          content: url(./component/picture/Menu.png);
          width: $w;
          height: $h;
          cursor: pointer;
        }
        div:nth-child(2) {
          margin-right: 0px;
        }
        div,
        img {
          margin-right: 17px;
        }
        .lang {
          padding: 0px;
          position: relative;
          width: 26px;
          height: 26px;
          background-color: white;
          top: 50%;
          transform: translate(0%, -50%);
          display: flex;
          justify-content: center;
          align-items: center;
          color: $color1;
          border-radius: 10px;
          cursor: pointer;
        }
      }
    }
    footer {
      position: absolute;
      width: 100vw;
      bottom: 17px;
      // background-color: red;
      div {
        // background-color: green;
        display: flex;
        justify-content: center;
        img,
        div {
          margin-right: 10px;
          margin-left: 10px;
        }
        img {
          content: url(./component/picture/paintPalette.png);
          width: 29px;
          height: 29px;
          cursor: pointer;
        }
        .lang {
          padding: 0px;
          position: relative;
          width: 26px;
          height: 26px;
          background-color: white;

          display: flex;
          justify-content: center;
          align-items: center;
          color: $color1;
          border-radius: 10px;
          cursor: pointer;
        }
      }
      @keyframes menueOpen {
        from {
          transform: translateX(230px);
        }
        to {
          transform: translateX(0px);
        }
      }
      @keyframes menueClose {
        from {
          transform: translateX(0px);
          
        }
        to {
          transform: translateX(230px);
          
        }
      }
      .menue {
        position: fixed;
        width: 230px;
        height: 100vh;
        background-color: #474752;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
        z-index: 2;
        right: 0px;
        top: 0px;
        // display: ;
        flex-wrap: wrap;
        display: flex;
        align-content: center;
        transform: translateX(230px);
        overflow: hidden;
        // animation: menue 2s forwards;
        
        color: white;


        span {
          width: 230px;
          height: fit-content;
          text-align: center;
          margin-top: 10px;
          
          cursor: pointer;

        }

        span:hover{
          color: $color2;
        }

        .close{
          position: absolute;
          left: -10px;
          width: 34px;
          height: 100vh;
          // background-color: $color1;
          border-right: 2pt solid $color1;
          display: flex;
          align-items: center;
          cursor: pointer;
          
          img{
            content: url('./component/picture/DoubleRight.png');
          }
        }
      }
    }
  }
}
